@mixin onDesktop {
  @media (min-width: 1260px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 744px) {
    @content;
  }
}

@mixin onMobile {
  @media (min-width: 320px) {
    @content;
  }
}

@mixin menu-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
