.move-free {
  &__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;
    margin: 0 0 32px;

    @include onTablet {
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 64px;
    }
  }

  &__content {
    font-weight: 500;
    font-size: 23px;
    line-height: 140%;
  }
}
